<template>
<div class="index paid-plans groundGray">
  <div class="scoll-layer">
    <div class="list_li paid-plans-li">
      <ul>
        <li>
          <label for="invitationCode">
            <div class="list_word">
              <div class="list_img page-checklist">
                <input type="radio" id="invitationCode" value="0" name="person" v-model="picked"><i></i>
              </div>
              <div><h3></h3></div>
              <span class="price"></span>
              <div class="list_num">
                <!-- <div class="paid-plans-post">/</div> -->
                <div class="list_view">
                  <a>&nbsp;</a>
                </div>
              </div>
            </div>
          </label>
        </li>
        <li>
          <label for="junior">
            <div class="list_word">
              <div class="list_img page-checklist">
                <input type="radio" id="junior" value="1" name="person" v-model="picked"><i></i>
              </div>
              <h3><span class="price"></span></h3>
              <!-- <div><h3></h3></div>
              <span class="price"></span> -->
              <div class="list_num">
                <!-- <div class="paid-plans-post">/</div> -->
                <div class="list_view">
                  <a></a>
                </div>
              </div>
            </div>
          </label>
        </li>
        <li>
          <label for="intermediate">
            <div class="list_word">
              <div class="list_img page-checklist">
                <input type="radio" id="intermediate" value="2" name="person" v-model="picked"><i></i>
              </div>
              <h3><span class="price">99</span></h3>
              <div class="list_num">
                <!-- <div class="paid-plans-post">/</div> -->
                <div class="list_view">
                  <a></a>
                </div>
              </div>
            </div>
          </label>
        </li>
        <li>
          <label for="advanced">
            <div class="list_word">
              <div class="list_img page-checklist">
                <input type="radio" id="advanced" value="3" name="person" v-model="picked"><i></i>
              </div>
              <h3><span class="price">500</span></h3>
              <div class="list_num">
                <!-- <div class="paid-plans-post">/</div> -->
                <div class="list_view">
                  <a href="javascript:void(0);"></a>
                </div>
              </div>
            </div>
          </label>
        </li>
      </ul>
    </div>
    <div class="btn_fix_bottom">
      <mt-button type="danger" size="large" @click.native="pay">{{picked}}</mt-button>
    </div>
  </div>
</div>
</template>

<script type="application/ecmascript">
import '../styles/sass/paid-plans.scss';
import wx from 'weixin-js-sdk';
import {
  Toast,
  MessageBox
} from 'mint-ui';
export default {
  name: 'page-checklist',
  data() {
    return {
      picked: '',
      urlType: '',
      id: {

      }
    };
  },
  created() {},
  methods: {
    //JS api 
    callpay(data) {
      const jsApiList = [
        'chooseWXPay'
      ];
      let mydata = JSON.parse(data);
      wx.config({
        // debug: true,
        appId: mydata.appId,
        // 
        timestamp: mydata.timeStamp,
        // 
        nonceStr: mydata.nonceStr,
        // 
        signature: mydata.paySign,
        // 1
        jsApiList: jsApiList
      });
      wx.chooseWXPay({
        appId: mydata.appId,
        timestamp: mydata.timeStamp, // jssdktimestamptimeStampS
        nonceStr: mydata.nonceStr, //  32 
        package: mydata.package, // prepay_idprepay_id=***
        signType: mydata.signType, // 'SHA1''MD5'
        paySign: mydata.paySign, // 
        success: function(res) {
          // 
          if (res.err_msg === 'get_brand_wcpay_request:ok') {
            MessageBox.alert('!').then(action => {
              this.$router.push({
                path: '/'
              });
            });
          } else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
            // message: "!"
            MessageBox.alert('!').then(action => {});
          } else {
            MessageBox.alert('!').then(action => {});
          }
        }
      });
    },
    pay() {
      // 
      if (this.picked === '0') {
        this.$router.push({
          path: '/coupon'
        });
      }
      let params;
      // 
      if (this.picked === '1') {
        this.urlType = this.$store.state.api.free_pay;
        params = {
          token: this.$store.state.auth.token,
          type: this.picked
        };
      } else {
        this.urlType = this.$store.state.api.wxpay;
        params = {
          token: this.$store.state.auth.token,
          type: this.picked,
          pay_type: 1
        };
      }
      this.axios.post(this.urlType, params)
        .then(response => {
          let succeed = response.data.status.succeed;
          if (succeed === '1') {
            if (this.picked === '1') {
              this.$router.push({
                path: '/'
              });
            } else {
              // 
              let data = response.data.data;
              console.info(data);
              this.callpay(data);
            }
          }
        }).catch(function(error) {
          console.log(error);
          Toast({
            message: '',
            position: 'bottom',
            duration: 5000
          });
        });
    }
  }
};
</script>
<style scoped lang='scss' rel="stylesheet/scss" type="text/css">
.list_word h3 {
    display: block;
    font-size: 20px;
    color: #333;
    height: 23px;
    line-height: 55px;
    overflow: visible;
}
</style>



