<template>
<div class="pay-deposit">
  <div class="pay_main">
    <h3><img src="../images/icon.png" @click="explain()"></h3>
    <ul class="pay_ul">
      <li class="pay_frist">
        <h3></h3><span>500</span>
      </li>
      <li class="pay_p">10,</li>
    </ul>
  </div>
  <!-- <a class="pay_inp" href="javascript:;" @click="pay()"></a> -->
  <div class="btn_fix_bottom">
    <mt-button type="danger" size="large" @click.native="pay()"></mt-button>
  </div>
  <!---->
  <div class="box_bg" :style="explainObject">
    <div class="pay_box">
      <div class="deposit_word">
        <h3></h3>
        <div class="box_word">
          <h3></h3>
          <p></p>
          <h3></h3>
          <p></p>
          <h3></h3>
          <p></p>
        </div>
        <a href="javascript:void(0);"><img class="guanbi" src="../images/icon_1.png" @click="closeExplain()" width="52"></a>
      </div>
    </div>
  </div>
  <mt-popup v-model="payWayVisible" position="bottom" class="mint-popup-4">
    <div class="pay_mode">
      <h3> <a href="javascript:void(0);"><img src="../images/icon_2.png" width="16" @click="closeMethod()" /></a></h3>
      <ul>
        <li>
          <a href="javascript:void(0);" style="color:#05cb04;" @click="payByWeixin()"><img src="../images/wechat1.png" width="24"/> </a>
        </li>
        <!-- <li>
          <a href="javascript:void(0);" style="color: #01a9ef;"><img src="../images/alipay1.png" width="24" />  <img class="img_1" src="../images/icon_3.png" width="7" /></a>
        </li> -->
      </ul>
    </div>
  </mt-popup>
</div>
</template>

<script type="application/ecmascript">
import '../styles/sass/pay-deposit.scss';
import {
  Indicator
} from 'mint-ui';
export default {
  data() {
    return {
      explainObject: {
        display: 'none'
      },
      payWayObject: {
        display: 'none'
      },
      payWayVisible: false
    };
  },
  created() {
    this.$store.dispatch('tabSwitcher', true);
  },
  methods: {
    explain() {
      this.explainObject.display = 'block';
    },
    closeExplain() {
      this.explainObject.display = 'none';
    },
    pay() {
      this.payWayVisible = true;
    },
    closeMethod() {
      this.payWayVisible = false;
    },
    payByWeixin() {
      console.info(this.$store.state.auth.token);
      this.payWayVisible = false;
      Indicator.open({
        text: '',
        spinnerType: 'fading-circle'
      });
      this.$router.push({
        path: '/'
      });
    }
  }
};
</script>
<style scoped lang='scss' rel="stylesheet/scss" type="text/css">
.mint-popup-4 {
    width: 100%;
    .picker-item,
    .picker-slot-wrapper {
        backface-visibility: hidden;
    }
}
</style>



